/* hero ========================================================================== */

.hero {
    background-image: url('/img/bg_hero.jpg');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    width: 100%;

		// .hero__content
		&__content {
      padding: 128px 0;
		}
		// .hero__descr
		&__descr {
      @include font (16px, 19px);
      font-weight: 700;
      color: $white;
      text-transform: uppercase;
      margin-bottom: 16px;
		}
		// .hero__title
		&__title {
      font-weight: 700;
      font-size: 64px;
      line-height: 82px;
      letter-spacing: -0.01em;
      text-transform: uppercase;
      color: $white;
      max-width: 680px;
      margin-bottom: 32px;
		}
		// .hero__btn
		&__btn {
      padding: 24px 64px;
      margin-bottom: 64px;
		}
		// .hero__text
		&__text {
      display: block;
      color: $white;
      max-width: 340px;
      position: relative;
      padding-left: 32px;
      &::before{
        content:"";
        width: 18px;
        height: 2px;
        background-color: $primary;

        position: absolute;
        top: 25%;
        left: 0;
      }
		}
}
