@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: local(""), url("../font/roboto-condensed-regular.woff2") format("woff2"), url("../font/roboto-condensed-regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: local(""), url("../font/roboto-condensed-bold.woff2") format("woff2"), url("../font/roboto-condensed-bold.woff") format("woff");
}
/* roboto-regular - latin_cyrillic */
html {
  font-size: 16px;
  font-family: "Roboto Condensed", sans-serif;
  line-height: 150%;
  font-weight: 400;
  font-style: normal;
  color: #161722;
}

.cf:after,
.cf:before {
  content: " ";
  display: table;
}

.cf:after {
  clear: both;
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
}
@media screen and (max-width: 992.98px) {
  .container {
    max-width: 720px;
  }
}
@media screen and (max-width: 767.98px) {
  .container {
    max-width: none;
    padding: 0 10px;
  }
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;
}

a {
  color: inherit;
  cursor: pointer;
}

img {
  max-width: 100%;
}

.btn {
  text-decoration: none;
  display: inline-block;
  color: transparent;
  border: 0;
  padding: 0;
  cursor: pointer;
  border-radius: 12px;
  padding: 20px 48px;
  text-transform: uppercase;
}

.btn--primary {
  background: #ffd2a4;
  backdrop-filter: blur(80px);
  font-size: 16px;
  line-height: 16px;
  color: #1C1D28;
  transition: all 0.3s ease-in-out;
}
.btn--primary:hover {
  background: #F2C79B;
}

.descr {
  font-size: 16px;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 400;
  font-style: normal;
  text-decoration: none;
  text-transform: uppercase;
}

.title {
  font-size: 48px;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 700;
  font-style: normal;
  line-height: 64px;
  letter-spacing: -0.01em;
  text-decoration: none;
  text-transform: uppercase;
}

.s-title {
  font-size: 24px;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 700;
  font-style: normal;
  letter-spacing: -0.01em;
  text-decoration: none;
  text-transform: uppercase;
}

.name {
  font-size: 32px;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 700;
  font-style: normal;
  line-height: 44px;
  letter-spacing: -0.01em;
  text-decoration: none;
  text-transform: uppercase;
}

.text {
  font-size: 16px;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 400;
  font-style: normal;
  line-height: 24px;
  text-decoration: none;
  text-transform: none;
}

.header {
  width: 100%;
  background-color: black;
  padding: 10px 0;
}
.header__flex {
  display: flex;
  align-items: center;
}
.header__logo {
  padding-right: 64px;
}
.header__nav {
  flex-grow: 1;
  margin-left: 24px;
}
.nav__item {
  display: inline-block;
  margin-left: 48px;
}
.nav__link {
  font-size: 16px;
  line-height: 1.1875;
  color: #ffffff;
  text-transform: uppercase;
  opacity: 0.9;
  transition: 0.3s;
}
.nav__link:hover {
  opacity: 1;
}

/* hero ========================================================================== */
.hero {
  background-image: url("/img/bg_hero.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  width: 100%;
}
.hero__content {
  padding: 128px 0;
}
.hero__descr {
  font-size: 16px;
  line-height: 1.1875;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  margin-bottom: 16px;
}
.hero__title {
  font-weight: 700;
  font-size: 64px;
  line-height: 82px;
  letter-spacing: -0.01em;
  text-transform: uppercase;
  color: #ffffff;
  max-width: 680px;
  margin-bottom: 32px;
}
.hero__btn {
  padding: 24px 64px;
  margin-bottom: 64px;
}
.hero__text {
  display: block;
  color: #ffffff;
  max-width: 340px;
  position: relative;
  padding-left: 32px;
}
.hero__text::before {
  content: "";
  width: 18px;
  height: 2px;
  background-color: #ffd2a4;
  position: absolute;
  top: 25%;
  left: 0;
}

/* wedo ========================================================================== */
.wedo {
  margin-top: 128px;
  margin-bottom: 128px;
}
.wedo__descr {
  text-align: center;
  margin-bottom: 16px;
}
.wedo__title {
  text-align: center;
  margin-bottom: 54px;
}
.wedo__blocks {
  display: flex;
  gap: 24px;
}

.block {
  width: 33.333%;
  background-color: #fff5eb;
  padding: 80px;
  border-bottom: 16px solid #ffd2a4;
}
.block__icon {
  width: 48px;
  height: 48px;
  background-color: #ffd2a4;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 24px;
}
.block__svg {
  width: 24px;
  height: 100%;
}
.block__name {
  color: #161722;
  margin-bottom: 8px;
}
.block__text {
  color: #161722;
}

/* welcome============================================================= */
.welcome {
  width: 100%;
  margin-bottom: 128px;
}
.welcome__content {
  text-align: center;
  max-width: 940px;
  margin: 0 auto;
}
.welcome__descr {
  margin-bottom: 16px;
}
.welcome__title {
  margin-bottom: 32px;
}
.welcome__text {
  margin-bottom: 56px;
}
.welcome__line {
  position: relative;
}
.welcome__btn {
  position: absolute;
  bottom: -25px;
  left: 50%;
  transform: translateX(-50%);
}
.welcome__grid {
  max-width: 1024px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 29% auto 29%;
  gap: 24px;
}
.welcome__img {
  width: 100%;
  height: 60%;
  object-fit: cover;
  object-position: center center;
  border-radius: 30px;
}
.welcome__poster--center {
  margin-top: 70px;
  height: 130%;
}
.welcome__down {
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
}
.welcome__subt {
  font-size: 16px;
  line-height: 1.1875;
  text-transform: uppercase;
  margin-bottom: 16px;
}
.welcome__name {
  margin-bottom: 16px;
}
.welcome__textdown {
  margin-bottom: 16px;
}
.welcome__more {
  font-size: 14px;
  line-height: 1.1428571429;
  position: relative;
}
.welcome__more::after {
  content: url("/img/svg/right.svg");
  width: 18px;
  height: 10px;
  top: 0;
  right: -30px;
  display: inline-block;
  position: absolute;
  color: #161722;
  fill: #161722;
}

/* benefits ========================================================================== */
.fits {
  width: 100%;
  padding: 128px 0;
  background-color: #f5f2f0;
}
.fits__descr {
  text-align: center;
  margin-bottom: 20px;
}
.fits__title {
  max-width: 440px;
  text-align: center;
  margin: 0 auto;
  margin-bottom: 64px;
}
.fits__grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 24px;
}
.poster {
  max-height: 384px;
  position: relative;
}
.poster__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.poster__content {
  position: absolute;
  bottom: 20px;
  color: #ffffff;
  padding: 0 32px;
}
.poster__s-title {
  margin-bottom: 16px;
}
.poster__text {
  opacity: 0.78;
}

