/* benefits ========================================================================== */

.fits {

  width: 100%;
  // margin-top: 128px;
  padding: 128px 0;
  background-color: $grey;
 
		// .benefits__descr
		&__descr {
      text-align: center;
			margin-bottom: 20px;
		}
		// .benefits__title
		&__title {
			max-width: 440px;
      text-align: center;
			margin: 0 auto;
			margin-bottom: 64px;
		}
		// .benefits__grid
		&__grid {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
			gap: 24px;

		}
		// .benefits__poster
		&__poster {

		}
}

.poster {
	max-height: 384px;
	position: relative;
	
		// .poster__img
		&__img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
		// .poster__content
		&__content {
			position: absolute;
			bottom: 20px;
			// text-align: center;
			color: $white;
			padding: 0 32px;
		}
		// .poster__s-title
		&__s-title {
			margin-bottom: 16px;
		}
		// .poster__text
		&__text {
			opacity: 0.78;
		}
}


