/* welcome============================================================= */

.welcome {
   width: 100%;
   margin-bottom: 128px;
		// .welcome__content
		&__content {
      text-align: center;
      max-width: 940px;
      margin: 0 auto;
		}
		// .welcome__descr
		&__descr {
      margin-bottom: 16px;

		}
		// .welcome__title
		&__title {
      margin-bottom: 32px;
      
		}
		// .welcome__text
		&__text {
      margin-bottom: 56px;
		}
    &__line{
      position: relative;
    }
		// .welcome__btn
		&__btn {
      position: absolute;
      bottom: -25px;
      left: 50%;
      transform: translateX(-50%);
		}
		// .welcome__grid
		&__grid {
      max-width: 1024px;
      margin: 0 auto;
      display: grid;
      grid-template-columns: 29% auto 29%;
      gap:24px;
		}
		// .welcome__poster
		&__poster:not:nth-child(2) {
      
      // height: 184px;
    }
		// .welcome__img
		&__img {
      width: 100%;
      height: 60%;
      object-fit: cover;
      object-position: center center;
      border-radius: 30px;
		}
		// .welcome__poster--center
		&__poster--center {
      margin-top: 70px;
      height: 130%;

		}

      // .welcome__down
		&__down {
         max-width: 600px;
         margin: 0 auto;
         text-align: center;
      }
      // .wecome__subt
      &__subt {
         @include font(16px,19px);
         text-transform: uppercase;
         margin-bottom: 16px;
      }
		// .welcome__name
		&__name {
         margin-bottom: 16px;
		}
		// .welcome__textdown
		&__textdown {
         margin-bottom: 16px;
		}
		// .welcome__more
		&__more {
         @include font (14px,16px);
         position: relative;
         &::after {
            content:url('/img/svg/right.svg');
            width: 18px;
            height: 10px;
            top: 0;
            right: -30px;
            display: inline-block;
            position: absolute;
            color: $black;
            fill: $black;
         }
		}
}


