.nav {
		// .nav__list
		&__list {
    }
		// .nav__item
		&__item {
      display: inline-block;
      margin-left: 48px;
		}
		// .nav__link
		&__link {
      @include font(16px,19px);
      color: $white;
      text-transform: uppercase;
      opacity: 0.9;
