/* wedo ========================================================================== */
.wedo {
  margin-top: 128px;
  margin-bottom: 128px;
		// .wedo__descr
		&__descr {

      text-align: center;
      margin-bottom: 16px;
		}
		// .wedo__title
		&__title {
      
      text-align: center;
      margin-bottom: 54px;
		}
		// .wedo__blocks
		&__blocks {
      display: flex;
      gap: 24px;
		}
}
.container {
}

.block {
  width: 33.333%;
  background-color: $light-orange;  
  padding: 80px;
  border-bottom: 16px solid $primary;
		// .block__icon
		&__icon {
      width: 48px;
      height: 48px;
      background-color: $primary;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 24px;

		}
		// .block__svg
		&__svg {
      width: 24px;
      height: 100%;
		}
		// .block__name
		&__name {
      color: $black;
      margin-bottom: 8px;
		}
		// .block__text
		&__text {
      color: $black;
		}

}

